@import 'normalize.css';
@import url('https://fonts.googleapis.com/css2?family=Crimson+Text:ital,wght@0,600;1,400;1,700&family=Roboto:ital,wght@0,100;0,300;0,400;0,500;0,700;1,100;1,400;1,700&family=Wix+Madefor+Text:ital,wght@0,400;0,500;0,600;1,400;1,500;1,600&display=swap');

* {
    margin: 0;
    padding: 0;
    box-sizing: border-box;
    color: white;
}

:root{
    --background-header:rgb(0,59,149);
    --box-shadow-color:#c9c3c3;
}

html *::-webkit-scrollbar {
    border-radius: 0;
    width: 8px;
}
html *::-webkit-scrollbar {
    border-radius: 0;
    width: 8px;
}
html *::-webkit-scrollbar-thumb {
    border-radius: 4px;
    background-color: rgba(0, 0, 0, 0.15);
}
html *::-webkit-scrollbar-thumb {
    border-radius: 4px;
    background-color: rgba(0, 0, 0, 0.15);
}
html *::-webkit-scrollbar-track {
    border-radius: 0;
    background-color: rgba(0, 0, 0, 0);
}
html *::-webkit-scrollbar-track {
    border-radius: 0;
    background-color: rgba(0, 0, 0, 0);
}
html {
    font-size: 62.5%;
    height: 100vh;
}

body {
    // font-family: 'Crimson Text', serif;
    font-family: 'Roboto', sans-serif;
    // font-family: 'Wix Madefor Text', sans-serif;
    font-size: 1.6rem;
    line-height: 1.5;
    text-rendering: optimizeSpeed;
    height: 100vh;
}

input,
button {
    border: none;
    outline: none;
}
button {
    cursor: pointer;
}

a[href] {
    text-decoration: none;
}

.container{
    display: flex;
    justify-content: center;

}

.grid{
    width: 80%;
}