.wrapper {
    display: flex;
    justify-content: center;
    align-items: center;
    width: 100%;
    height: 80vh;

    .loading-icon {
        animation: loading 1s ease-in-out infinite;
        font-size: 2rem;
    }
    .smallSize {
        font-size: 1rem;
    }
    .bigSize {
        font-size: 4rem;
    }
}

@keyframes loading {
    0% {
        transform: rotate(0);
    }
    100% {
        transform: rotate(360deg);
    }
}
