.wrapper {
    background-color: var(--background-header);
    padding: 10px 0;
}

.contain {
    .first-line {
        display: flex;
        justify-content: space-between;
        align-items: center;

        .convenience {
            display: flex;
            // grid-template-columns: auto auto auto auto auto auto;
            // column-gap: 10px;

            .currency,
            .flag,
            .help-customer,
            .post-place {
                &:hover {
                    opacity: 0.7;
                }
            }

            .currency,
            .flag,
            .help-customer,
            .post-place,
            .register,
            .login {
                cursor: pointer;
                margin: auto;
                margin: 0px 10px;
                padding: 3px;
            }
            .btn-change-currency,
            .btn-change-country {
                background-color: transparent;
            }
            .flag {
                display: block;
                width: 33px;
                height: 33px;
                border-radius: 50%;
            }
            .help-customer {
                font-size: 2.4rem;
            }
            .link-post-place {
                display: flex;
                align-items: center;
                .post-place {
                    font-size: 1.8rem;
                    font-weight: 500;
                }
            }
            .register,
            .login {
                transition: all 0.2s ease-in;
                width: auto;
                padding: 3px 10px;
                height: 35px;
                color: black;
                border-radius: 5px;
                font-size: 1.5rem;
                &:hover {
                    box-shadow: 3px 3px 5px var(--box-shadow-color);
                    transform: translateY(-3px);
                }
            }
        }
        .convenience,
        .logo-title {
            padding: 0 10px;
        }
    }
}

.last-line {
    display: flex;
    justify-content: left;
    .wrapper__last-line {
        padding: 10px 20px;
        border-radius: 20px;
        .icon__last-line {
            margin-right: 7px;
        }
        &:hover {
            background-color: rgba(245, 235, 235, 0.3);
        }
    }
    .active {
        border: 1px solid rgb(245, 239, 239);
        background-color: rgba(245, 235, 235, 0.3);
    }
}

.first-line {
    margin-bottom: 20px;
}
